
.companyName__changePr{
    display: flex;
    align-items: center;
}
.newsfeed__chart {
    margin-left: 20px;
}
.Follow__container{
    width: min-content;
    margin-left: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(26, 26, 26);
    border-top: 1px solid rgb(51, 51, 51);
    border-bottom: 1px solid rgb(51, 51, 51);
    padding: 5px 10px;
    border-radius: 20px;
    cursor: pointer;
}
.Follow__container:hover {
    background-color: rgb(43, 43, 43);
    border-top: 1px solid rgb(100, 100, 100);
    border-bottom: 1px solid rgb(100, 100, 100);
}
.Follow__container p{
    margin: 0px 5px;
    font-size: 18px;
}
.stats__follow{
    display:'flex';
    justify-content:'space-between';
}
.bottom__container {
    color: white;
    height: auto;
    width: 100%;
    justify-content: start;
}
.bottom__container p{
    color: white;
}
.info__container {
    height: auto;
}
.one__stat {
    display: flex;
    justify-content: space-between;
    height: auto;
    width: 80%;
    padding: 5px 10px;
    margin: 10px;
    margin-bottom: 25px;
    color: rgb(214, 214, 214);
    border-radius: 5px;
}
@media only screen and (max-width: 600px) {
    .companyName__changePr{
        display: block;
        align-items: center;
    }
    .main__changePr__green{
        width: min-content;
        margin-left: 20px;
    }
    .main__changePr__red{
        margin-left: 20px;
        width: min-content;
    }
    .newsfeed__chartsection{
        height: 100vh;
    }
    .bottom__container {
        height: 100vh;
        margin-top: 20px;
    }
    .info__container{
        justify-content: space-between;
    }
    .one__stat {
        margin: 0px;
        width: 103%;
        margin-top: 5px;
        border-radius: 0;
        padding: 5px 0px;
    }
    .Follow__container{
        height: min-content;
        width: min-content;
        padding: 4px;
        background-color: azure;
        border:1px solid rgb(186, 186, 255); 
    }
    .Follow__container:hover{
        width: min-content;
        padding: 4px;
        background-color: azure;
        border:2px solid rgb(186, 186, 255); 
    }
    .eye{
        float: right;
    }
    .Follow__container p{
        display: none;
        visibility: hidden;
    }
}
.news__container{
    height: 100%;
    margin-left: 20px;
}
.allNews__container{
    height: 100vh;
    width: 73vw;
}
.allNews__container::-webkit-scrollbar {
    width: 3px;
    height: 0px;
}
.allNews__container::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(0, 0, 0);
    border-radius: 10px;
}
.allNews__container::-webkit-scrollbar-thumb {
    background: rgb(97, 97, 97);
    border-radius: 10px;
}
.allNews__container::-webkit-scrollbar-thumb:hover {
    background: #444444;
}

.oneNews__container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 0px;
    border-radius: 10px;
    height: auto;
    width: 100%;
    margin-bottom: 10px;
    cursor: pointer;
}
@media only screen and (max-width: 600px) {
    .oneNews__container{ 
        display: flex;
        width: 90vw;
        height: 12vh;
        margin-bottom: 25px;
    }
    .oneNews__container img{
        height: 90px;
        width: 90px;
        margin-bottom: 3px;
    }
    .oneNews__container p{
        font-size: 0.5rem;
        margin-bottom: 5px;
    }
    .news__container{
        margin-left: 20px;
    }
}
.oneNews__container p{
    color: rgb(130, 130, 130);
}
.chart__container{
    display: flex;
    left: -10px;
}
.chart{
    width: 900px;
}
.desc{
    margin-top:10px;
    margin-bottom:10px;
    margin-left: 20px;
    height: 35px;
    width: 73vw;
    padding: 5px 0px;
    color: white;
    overflow-x: hidden;
    overflow-y: hidden;
}
.desc p{
    font-size: 1rem;
}
@media only screen and (max-width: 600px) {
    .newsfeed__chartsection{
        height: min-content;
    }
    .chart__container{
        display: block;
        padding: 0;
    }
    .chart{
        width: 100vw;
        margin-left: -17px;
    }
    .info__container{
        font-size: large;
        margin-left: 10px;
    }
    .desc{
        margin-top: 20px;
        margin-left: 20px;
        height: 35px;
        width: 90vw;
        padding: 5px 0px;
        color: white;
        overflow-x: hidden;
        overflow-y: hidden;
    }
    .desc p{
        font-size: 1rem;
    }
}