
.sug_list__container{
    display: flex;
    width: 100vw;
    overflow-x: scroll;
    margin-bottom: 20px;
    min-height: 140px;
}
.one__sug__container{
    min-height: 80px;
    min-width: 120px;
    max-height: 80px;
    max-width: 80px;
    padding:15px;
    margin-right: 10px;
    border:1px solid gray;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    text-align: left;
    cursor: pointer;
}
.sug_list__container::-webkit-scrollbar {
    width: 0px;
    height: 0px;
}
.sug_list__container::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(0, 0, 0);
    border-radius: 10px;
}
.sug_list__container::-webkit-scrollbar-thumb {
    background: rgb(97, 97, 97);
    border-radius: 10px;
}
.sug_list__container::-webkit-scrollbar-thumb:hover {
    background: #444444;
}
