
.one_comp {
    height: auto;
    width: auto;
    background-color: rgb(0, 0, 0);
    display: flex;
    padding: 10px 20px;
    margin: 0px 15px;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid black;
    cursor: pointer;
    color: rgb(130, 130, 130);
}
.btn{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
@media only screen and (max-width: 600px) {
    .one_comp {
    width:90vw ;
    margin: 0;
    background: rgba( 0, 0, 0, 0);
    border: none;
    }
}
.one_comp:hover {
    background-color: rgb(16, 16, 16);
    color: chartreuse;
}
.header {
    width: 100%;
    padding: 0;
    margin: 0;
}
.header h1 {
    color: rgb(130, 130, 130);
    padding: 0;
    margin: 0;
}
.btn p {
    font-size: 15px;
    color: chartreuse;
    margin-left: 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.info__current p {
    margin-left: 20px;
    font-size: 10px;
    margin-top: 0;
    color: white;
    margin-bottom: 0;
    padding: 0;
}
.info__current h1 {
    margin-left: 20px;
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 15px;
}
p {
    margin-left: 0;
    font-size: 10px;
    margin-top: 0;
    color: rgb(130, 130, 130);
    margin-bottom: 0;
    padding: 0;
}
h1 {
    color: rgb(255, 255, 255);
    margin-top: 0;
    margin-bottom: 5px;
    font-size: 15px;
}
.jASeVL{
    border: none;
}
.Main__stats__container{
    border: none;
}