body {
    min-width: 100%;
    min-height: 100%;
    margin: 0;
    background-color: rgb(5, 5, 5);
    display: flex;
    font-family:'Google Sans',Roboto,Arial,sans-serif;
    z-index: 0;
}
body::-webkit-scrollbar {
    display: none;
}
.app__body {
    height: 100vh;
    width: 100vw;
    min-width: 100vw;
    display: flex;
    justify-self: center;
    overflow-y: hidden;
    overflow-x: hidden;
}
.newsFeed {
    margin-top: 8vh;
    height: 93vh;
    flex: 0.8;
    color: white;
    margin-right: 20px;
    overflow-y: scroll;
    overflow-x: hidden;
}
.newsFeed::-webkit-scrollbar {
    width: 1px;
}
.newsFeed::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(0, 0, 0);
    border-radius: 10px;
}
.newsFeed::-webkit-scrollbar-thumb {
    background: rgb(97, 97, 97);
    border-radius: 10px;
}
.stats::-webkit-scrollbar-thumb:hover {
    background: #444444;
}

.canvasjs-chart-credit{
    color:'black'
}
.stats {
    margin-top: 8vh;
    flex: 0.2;
    min-width:fit-content;
    overflow-y: scroll;
}
.stats::-webkit-scrollbar {
    width: 3px;
}
.stats::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgb(0, 0, 0);
    border-radius: 10px;
}
.stats::-webkit-scrollbar-thumb {
    background: rgb(97, 97, 97);
    border-radius: 10px;
}
.stats::-webkit-scrollbar-thumb:hover {
    background: #444444;
}
@media only screen and (max-width: 600px) {
    body {
        display: block;
        overflow-y: hidden;
    }
    .app__body {
        margin-top: 10px;
        display: block;
        overflow-y: scroll;
    }
    .newsFeed { 
        margin-top: 0;
        padding-top: 6vh;
        scroll-snap-align: start;
        overflow-y: scroll;
        flex: none;
        overflow-y: visible;
        overflow-x: visible;
    }
    .stats {
        flex: none;
        height: 100vh;
        width: 100vw;
        position: fixed;
        margin-top:0;
        flex: 0;
        min-width:fit-content;
        overflow-y: scroll;
        z-index: 600;
    }
    .App{
        display: flex;
    }
}