.header__wrapper {
    background: rgba(0, 0, 0, 0.659);
    backdrop-filter: blur( 6px );
    -webkit-backdrop-filter: blur( 6px );
    border-bottom: 1px solid rgba(255, 255, 255, 0.254);
    height: 55px;
    width: 100vw;
    position: fixed;
    z-index: 100;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
@media only screen and (max-width: 1440px) {
.input__container{
    padding: 0;
    left: 30px;
    width: 100px;
    height: 35px;
    border-radius: 5px;
    border:none;
    display: flex;
}
}
.header__wrapper div {
    display: flex;
    color: rgb(255, 255, 255);
}
.header__wrapper .logo {
    display: flex;
    align-items: center;
    color:gray;
    margin-left: 40px;
    font-size:x-large;
    font-weight: bold;
    font-family: 'Trebuchet MS', sans-serif;
  }
.input__container{
    padding-left: 10px;
    width: 410px;
    height: 35px;
    border-radius: 5px;
    border:none;
    display: flex;
}
.inputBox{
    margin: 0px;
    padding: 5px;
    font-size: large;
    color:rgb(255, 255, 255);
    width: 400px;
    height: 25px;
    border-radius: 5px;
    border:none;
    background-color: rgba( 255, 255, 255, 0 );
}
input:focus {
    outline: none;
}
.header__wrapper .logOut{
    color:rgb(0, 0, 0);
    padding: 5px;
    font-family: 'Trebuchet MS', sans-serif;
    border-radius: 5px;
}
@media only screen and (max-width: 600px) {
    .logo{
        visibility:hidden;
        display: none;
    }
    .header__wrapper{
        width: 100vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .logOut{
        visibility: hidden;
        display: none;
    }
    .ham{
        display:block;
        position: absolute;
        left: 20px;
        top:1.5vh;
        visibility:visible;
    }
    .input__container{
        padding:5px;
        width: 50vw;
        margin-left: -55px;
        margin-top: 5px;
    }
}